import React, { useState, useEffect } from 'react';
import axios from 'axios';

import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';

function Users() {
  const [users, setUsers] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        // ÏöîÏ≤≠Ïù¥ ÏãúÏûë Ìï† ÎïåÏóêÎäî error ÏôÄ users Î•º Ï¥àÍ∏∞ÌôîÌïòÍ≥†
        setError(null);
        setUsers(null);
        // loading ÏÉÅÌÉúÎ•º true Î°ú Î∞îÍøâÎãàÎã§.
        setLoading(true);
        const response = await axios.get(
          'http://sulrae.com/api/shop'
        );
        setUsers(response.data); // Îç∞Ïù¥ÌÑ∞Îäî response.data ÏïàÏóê Îì§Ïñ¥ÏûàÏäµÎãàÎã§.
      } catch (e) {
        setError(e);
      }
      setLoading(false);
    };

    fetchUsers();
  }, []);

  if (loading) return <div>Î°úÎî©Ï§ë..</div>;
  if (error) return <div>ÏóêÎü¨Í∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§</div>;
  if (!users) return null;
  return (
    <ul>
      {users.map(user => (
        <li key={user.id}>
          {user.name}
        </li>
        // <ListItem  button key={user.id}>
        //            <br/>
        // Îß§Ïû•Î™Ö : {user.username} <br/>
        // ÌèâÏ†ê : 5<br/>
        // ÏúÑÏπò : {user.address.street}<br/>
        // Ï†ÑÌôîÎ≤àÌò∏ : {user.phone}

        // </ListItem>
      ))}
    </ul>
  );
}

export default Users;